<template>
  <v-content>
    <v-container>
      <v-card class="secondary">
        <v-card-title
          primary-title
          class="about__title white--text"
        >
          About PopUpPosy
        </v-card-title>
        <v-card class="about__subtitle-block">
          <v-card-title class="about__subtitle-text">The Rentals:</v-card-title>
          <v-card-text class="about__description">
            PopUpPosy Rentals is an upcoming project by Alexandra Menard (but you can call her
            Alex).
            She will be providing signs, decorations, and other event materials that have been
            created,
            modified, or
            maintained by Alex.
          </v-card-text>
        </v-card>
        <v-divider light></v-divider>
        <v-card class="about__subtitle">
          <v-card-title class="about__subtitle-text">The Artisan:</v-card-title>
          <v-card-text class="about__description">
            Alexandra Menard is a calligrapher, painter, and designer.<br/>
            Alex was born Canadian, moved to Las Vegas as a child, then to Northern California after
            college at the
            University of Nevada, Reno.<br/>
            She will be moving to the Denver, CO area very soon with her Husband and will be
            starting
            her rental service
            there sometime early in 2019.<br/>
            Alex loves dogs, her family, Harry Potter, and the color pink.<br/>
          </v-card-text>
        </v-card>
        <v-divider light></v-divider>
        <v-card class="about__subtitle">
          <v-card-title class="about__subtitle-text">The Shop:</v-card-title>
          <v-card-text class="about__description">
            Alex has an etsy shop where she sells her calligraphy and paint projects, as well as
            commissioned projects.
            You can check out her shop <a target="_blank"
                                          href="https://www.etsy.com/shop/PopUpPosy">here</a>
          </v-card-text>
        </v-card>
      </v-card>
    </v-container>
  </v-content>
</template>

<script>
export default {
  name: 'About',
};
</script>

